const Yao = ({ yang, change }) => {
    const clx = yang ? 'yao' : 'yao yin'
    const changeClx = change ? 'changing' : ''
    return <Row className={`${clx} ${changeClx}`}>
        <Col></Col>
        <Col> </Col>
        <Col></Col>
    </Row>
}

const chunk = (arr, len) => {

    var chunks = [],
        i = 0,
        n = arr.length;

    while (i < n) {
        chunks.push(arr.slice(i, i += len));
    }

    return chunks;
}

const Component = ({
    trigram,
    title,
    changeLine,
}) => {

    const trigramElement =  {
        '111': 'ME', 
        '110': 'WO', 
        '101': 'FI', 
        '100': 'WO', 
        '011': 'ME', 
        '010': 'WA',  
        '001': 'EA', 
        '000': 'EA', 
    }

    return <>
        {
            chunk(trigram?.split('').reverse(), 3)
                .map((tri, index) => <div className={`trigram ${trigramElement[tri.join('')]}`}>
                    {
                        tri.map((e, i) => <Yao
                            change={changeLine - 1 === (index * 3) + i}
                            yang={1 === Number(e) ? true : false}></Yao>)
                    }
                </div>

                )
        }
        {
            title
        }

    </>

}

export default Component
